const Wrapper = styled.div `
  .drawer{

    padding-top: 70px;
}

`
class FileSearchMenu extends React.Component {

  constructor(props) {
    super(props)
    this.state = {
      open: false
    };

  }

  handleToggle = () => this.setState({
    open: !this.state.open
  });

  componentDidMount() {}

  render() {

    let schema = [
      {
        'category': 'Grupos Biológicos',
        'items': [
          {
            name: 'Anfibios',
            value: '1'
          }, {
            name: 'Aves',
            value: '2'
          }, {
            name: 'Hongos',
            value: '3'
          }, {
            name: 'Insectos',
            value: '4'
          }, {
            name: 'Mamíferos',
            value: '5'
          }, {
            name: 'Peces',
            value: '6'
          }, {
            name: 'Plantas',
            value: '7'
          }, {
            name: 'Reptiles',
            value: '8'
          }
        ]
      }, {
        'category': 'Departamentos',
        'items': [
          {
            name: 'Cundinamarca',
            value: '1'
          }, {
            name: 'Boyacá',
            value: '2'
          }
        ]
      }, {
        'category': 'Ecosistemas',
        'items': [
          {
            name: 'Bosque tropical',
            value: '1'
          }, {
            name: 'Desierto',
            value: '2'
          }, {
            name: 'Bosque seco',
            value: '3'
          }, {
            name: 'Páramo',
            value: '4'
          }, {
            name: 'Bosque de niebla',
            value: '5'
          }, {
            name: 'Humedales',
            value: '6'
          }
        ]
      }
    ];

    return (
      <Wrapper>
        <RaisedButton label="Toggle Drawer" onTouchTap={this.handleToggle}/>
        <Drawer open={this.state.open}>
          <br/>
          <br/>
          <br/>
          <br/>
          <List>
            <Subheader>Filtros activos</Subheader>
            {schema.map((record, i) => (
              <div key={i}>

                {record.items.map((subrecord, j) => (
                  <div key={j}>
                    {subrecord.name}
                  </div>
                ))}
                {fff = 3}

                <ListItem
                  primaryText={record.category}
                  leftIcon={< ActionGrade />}
                  initiallyOpen={true}
                  primaryTogglesNestedList={true}
                  nestedItems={[]}
              />

              </div>
            ))}
          </List>
        </Drawer>
      </Wrapper>
    )
  }
}

export default FileSearchMenu

